    align-items: center;
    display: flex;
    flex:1;
    background-size: cover;


}
.loginForm{
    background-color: #ffffff3d;
    padding:30px;
    border:1px solid rgb(81, 76, 76);
    border-radius: 1px;

}
.loginForm2{
    background-color: #fff;
    padding:30px;
