img{
    width:10%;
    object-fit:contain;
}
.navbar{
    display:flex;
    justify-content: space-around;
    align-items: baseline;
    box-shadow: 1px 5px 5px #e0dcdc;
}
ul{
    display:flex;
    align-items: baseline;
}
li{
    list-style: none;
    padding:1px 20px;
}
a{
    text-decoration: none;
    color:black;
    font-size: 18px;
}
.primaryBtn{
    cursor:pointer;
    font-weight: bolder;
    padding:13px 25px;
    font-size:0.8rem;
    border:none;
    color:white;
    background: #db183c;
    border-radius: 15px;
    margin-left:20px;
    transition: all 0.25s ease;
}
.primaryBtn:hover{
    transform: translateY(-5px);
    box-shadow: 0 10px 20px -10px rgba(219,24,44,0.6);
}
.material-symbols-outlined {
    font-size: 24px;
    vertical-align: middle;
  }
  
  .nav-menu li {
    display: flex;
    align-items: center;
    padding: 0 10px;
  }